<script>
  import { slide } from 'svelte/transition'
  import marked from 'marked'
  import { sanitize } from '../helpers/sanitize.js'
  import { navigate } from './Router.svelte'

  export let card

  $: cardHtml = marked(sanitize(card.content))
  $: cardIsEmpty = card.content

  let transitionTime =
    getComputedStyle(document.body)
      .getPropertyValue('--transition-time')
      .slice(0, -1) * 1000
</script>

<style>
  .card {
    width: 100%;
    padding: 1rem;
    padding-top: 1.5rem;
    padding-bottom: 0.75rem;
    display: block;

    color: inherit;
    background: var(--background);

    border: none;
    border-bottom: 1px solid var(--border-color);

    font-size: 1rem;
    font-family: inherit;
    width: 100%;
    text-align: left;

    cursor: pointer;

    z-index: 10;
  }

  p.empty {
    color: var(--color-secondary);
  }

  /* Markdown styles */
  .card :global(p, ul, ol, hr, table) {
    margin-bottom: 0.75rem;
    line-height: var(--line-height);
  }

  .card :global(h1),
  .card :global(h2),
  .card :global(h3),
  .card :global(h4),
  .card :global(h5),
  .card :global(h6) {
    margin-bottom: 0.75rem;
    line-height: 1.3;
  }

  .card :global(h1) {
    font-size: 1.85rem;
  }
  .card :global(h2) {
    font-size: 1.7rem;
  }
  .card :global(h3) {
    font-size: 1.55rem;
  }
  .card :global(h4) {
    font-size: 1.4rem;
  }
  .card :global(h5) {
    font-size: 1.25rem;
  }
  .card :global(h6) {
    font-size: 1.1rem;
  }

  .card :global(a) {
    text-decoration: none;
    color: var(--primary);
  }

  .card :global(a:hover),
  .card :global(a:focus) {
    text-decoration: underline;
  }

  .card :global(ul, ol) {
    list-style-position: inside;
  }
  .card :global(ul) {
    list-style-type: circle;
  }

  .card :global(hr) {
    height: 0;
    margin-bottom: 0.75rem;
    border: none;
    border-top: 1px dashed var(--border-color);
  }
</style>

<button
  class="card"
  transition:slide|local={{ duration: transitionTime, delay: transitionTime / 1.5 }}
  on:click={() => navigate('/editor?id=' + card.id)}>

  {#if cardIsEmpty}
    {@html cardHtml}
  {:else}
    <p class="empty">Empty</p>
  {/if}
</button>
