<script>
  import { scale, fade } from 'svelte/transition'
  import { expoOut } from 'svelte/easing'

  let transitionTime =
    getComputedStyle(document.body)
      .getPropertyValue('--transition-time')
      .slice(0, -1) * 1000
</script>

<style>
  .scroll-view {
    position: absolute;
    width: 100%;
    height: 100vh;
    height: -webkit-fill-available;
    overflow-y: overlay;
    overflow-x: hidden;
    background: var(--background);
  }
</style>

<div
  class="scroll-view"
  in:scale={{ delay: 0, duration: transitionTime * 1.75, opacity: 0, start: 1.075, easing: expoOut }}
  out:scale={{ delay: 0, duration: transitionTime / 1.5, opacity: 0, start: 0.975, easing: expoOut }}>
  <slot />
</div>
