<script>
  import { onMount } from 'svelte'
  import { fade } from 'svelte/transition'
  import { cards } from '../cards'
  import { params, navigate } from '../components/Router.svelte'
  import Button from '../components/Button.svelte'
  import ScrollView from '../components/ScrollView.svelte'
  import Header from '../components/Header.svelte'

  let editor

  if (!Boolean($params.get('id'))) {
    cards.add('md')
    navigate('/editor?id=' + $cards[0].id, true)
  }

  $: card = $cards.find((c) => c.id === parseInt($params.get('id')))
  $: cardIsExist = Boolean(card)

  onMount(() => {
    setTimeout(() => editor.focus(), 500)
    autoResize()
  })

  const autoResize = () => {
    editor.style.height = 'auto'
    editor.style.height = editor.scrollHeight + 'px'
  }

  const navigateBack = () => {
    card.content ? null : cards.remove(card.id)
    window.history.back()
  }

  const deleteCard = () => {
    cards.remove(card.id)
    window.history.back()
  }
</script>

<style>
  textarea {
    overflow: hidden;
    resize: none;

    width: 100%;
    height: auto;

    padding: 1.5rem 0;

    color: inherit;
    background: var(--background);
    border: none;

    font-size: 1rem;
    font-family: inherit;
    line-height: var(--line-height);
  }
</style>

<ScrollView>
  <Header>
    <Button
      icon="navigate_before"
      class="transparent"
      on:click={navigateBack} />
    <Button icon="delete" class="transparent" on:click={deleteCard} />
  </Header>

  <article>
    <div class="container">
      {#if cardIsExist}
        <textarea
          name="Editor"
          placeholder="Type here..."
          out:fade={{ duration: 150 }}
          on:input={autoResize}
          on:input={(card.changed = Date.now())}
          bind:value={$cards[$cards.indexOf(card)].content}
          bind:this={editor} />
      {/if}
    </div>
  </article>
</ScrollView>
