<script>
  import Icon from './Icon.svelte'

  export let icon = null

  let isIconExists = Boolean(icon)
  let isTextExists = Boolean($$props.$$slots)
</script>

<style>
  button {
    display: inline-flex;
    justify-content: center;
    align-items: center;
    box-sizing: border-box;
    vertical-align: middle;
    padding: 0.3125rem;

    border: 1px solid transparent;
    border-radius: 15rem;
    outline: none;

    font-size: 1rem;
    font-family: inherit;
    line-height: 1;

    cursor: pointer;

    transition: transform var(--transition-time),
      background var(--transition-time), border var(--transition-time);
  }

  button:active {
    transform: scale(0.9);
  }

  button.text span {
    display: inline-flex;
    margin: 0 3px;
    font-weight: bold;
    line-height: 1.5;
    vertical-align: top;
  }

  /* Size */
  button {
    padding: 0.3125rem;
  }

  button.text {
    padding: 0.3125rem 0.5rem;
  }

  button.small {
    padding: 0.1875rem;
  }

  button.text.small {
    padding: 0.1875rem 0.5rem;
  }

  button.big {
    padding: 0.5rem;
  }

  button.text.big {
    padding: 0.5rem 0.5rem;
  }

  /* Default */
  button {
    color: var(--color);
    background: var(--surface);
    border-color: var(--surface);
  }

  button:hover,
  button:focus {
    color: var(--color);
    background: var(--surface);
    border-color: var(--border-color);
  }

  /* Outlined */
  button.outlined {
    color: var(--color);
    background: transparent;
    border-color: var(--border-color);
  }

  button.outlined:hover,
  button.outlined:focus {
    color: var(--color);
    background: var(--surface);
    border-color: var(--border-color);
  }

  /* Transparent */
  button.transparent {
    color: var(--color);
    background: transparent;
    border-color: transparent;
  }

  button.transparent:hover,
  button.transparent:focus {
    color: var(--color);
    background: var(--surface);
    border-color: transparent;
  }
</style>

<button
  on:click
  class={$$props.class || ''}
  class:icon={isIconExists}
  class:text={isTextExists}>
  {#if isIconExists}
    <Icon>{icon}</Icon>
  {/if}

  {#if isTextExists}
    <span>
      <slot />
    </span>
  {/if}
</button>
